.box{
    max-width: 40em;
    width: 100%;
    height: fit-content;
    padding: 0.2em 2em 0.5em 2em;
    background-color: #2d2d30;
    color: #c7c2c2;
    border-radius: 8px;
    text-align: left;
    margin: auto;
    margin-bottom: 1em;
}
.title{
    border-bottom: #c7c2c2 solid 3px;
    margin-top: 0.1em;
}
.titleIcon {
    margin-right: 0.3em;
}
.foundIcon{
    color: green;
}
.details {
    margin: 0.5em 0 0.5em 0.3em;
}
.link{
    color: #c7c2c2;
    text-decoration: none;
}
.link:hover{
    color: rgb(171, 165, 165);
    text-decoration: none;
}
.link:active{
    color: #817d7d;
    text-decoration: none;
}
.imagePreview{
    width: 100%;
    min-height: 20em;
    object-fit: cover;
    border-radius: 1em;
}
.imageBox{
    position: relative;
    background-color: #1e1e20;
    border-radius: 1em;
}
.loading{
    color: #c7c2c2;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: fit-content;
    position: absolute;
    top: 40%;
    left: 40%;
    z-index: 100;
}
.loading > p {
    display: inline-block;
    position: relative;
    margin: 0;
    padding: 0;
    top: 65px
}
.profilePic{
    width: 1.5em;
    height: 1.5em;
    border-radius: 100%;
    margin-right: 0.5em;
}
.postedby {
    align-items: center;
    margin: auto;
}
.userlink{
    color: #94aacc;
    text-decoration: none;
    display: inline-block;
    margin: 0;
    cursor: pointer;
}
.userlink:hover{
    color: #70819b;
    text-decoration: none;
}
.userlink:active{
    color: #99b6e1;
    text-decoration: none;
}